.main-footer {
  padding: 59px 0 70px 0;
  background-color: #222222;
}
@media (min-width: 768px) {
  .main-footer {
    padding: 60px 0 109px 0;
  }
}

.main-footer__wrapper {
  padding: 0 29px;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  grid-template-rows: repeat(3, auto);
}
@media (min-width: 768px) {
  .main-footer__wrapper {
    padding: 0;
    display: grid;
    grid-template-columns: auto 6.11% 1fr 12.7% auto;
    grid-template-rows: repeat(2, auto);
  }
}
@media (min-width: 1200px) {
  .main-footer__wrapper {
    grid-template-columns: 22.47% 5.11% 45.06% 9.7% 17.66%;
    grid-template-rows: auto;
  }
}

.main-footer__contacts {
  display: inline-flex;
  flex-direction: column;
  gap: 32px;
  text-align: left;
  margin-bottom: 62.5px;
}
@media (min-width: 768px) {
  .main-footer__contacts {
    grid-column: 1/2;
    margin: 0;
  }
}

.main-footer__logo {
  display: none;
}
@media (min-width: 768px) {
  .main-footer__logo {
    display: block;
  }
}

.main-footer__email-wrapper,
.main-footer__address-wrapper,
.main-footer__work-hours-wrapper {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 16px;
  color: #e9e9e9;
  font-size: 15px;
  font-weight: 300;
}
.main-footer__email-wrapper svg,
.main-footer__address-wrapper svg,
.main-footer__work-hours-wrapper svg {
  margin-top: 2px;
}

.main-footer__work-hours-wrapper {
  margin-bottom: 32px;
}

.main-footer__email {
  color: #ff812e;
}

.main-footer__address {
  font-style: normal;
}

.main-footer__button {
  padding: 13px 20px 11px 18px;
  min-width: 215px;
  width: 57.18vw;
  align-self: center;
}
.main-footer__button svg {
  width: 16px;
  height: 20px;
  margin-right: 22px;
}
@media (min-width: 768px) {
  .main-footer__button {
    min-width: auto;
    width: auto;
    max-width: 228px;
    align-self: start;
  }
}

.main-footer__info {
  display: inline-flex;
  flex-direction: column;
  margin-bottom: 52px;
}
@media (min-width: 768px) {
  .main-footer__info {
    grid-column: 5/6;
    margin: 0;
  }
}

.main-footer__tel {
  color: #e9e9e9;
  font-size: 20px;
  font-weight: 900;
  margin-bottom: 6px;
}

.main-footer__work-hours span {
  display: block;
  font-size: 14px;
  text-align: left;
}

.main-footer__call {
  display: inline-block;
  color: #ff812e;
  align-self: center;
  margin-bottom: 32px;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  user-select: none;
}

.main-footer__social {
  margin: 0 auto;
}

.main-footer__copyright {
  text-align: left;
  font-size: 14px;
}
.main-footer__copyright p {
  margin: 0 0 19px 0;
  color: #595959;
}
.main-footer__copyright span {
  color: #e9e9e9;
}
@media (min-width: 768px) {
  .main-footer__copyright {
    grid-column: 1/-1;
    grid-row: 2/3;
    margin-top: 64px;
  }
}
@media (min-width: 1200px) {
  .main-footer__copyright {
    grid-column: 3/4;
    grid-row: 1/2;
    margin: 0;
  }
}

.social {
  list-style-type: none;
  margin: 0 auto;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, 20px);
  gap: 23.5px;
  align-items: center;
}
@media (min-width: 768px) {
  .social {
    grid-template-columns: repeat(4, 20px) 17px;
  }
}

.social__item--telegram {
  display: none;
}
@media (min-width: 768px) {
  .social__item--telegram {
    display: flex;
  }
}

.promo {
  padding: 32px 0 64px 0;
}
@media (min-width: 768px) {
  .promo {
    padding: 60px 0 80px 0;
  }
}

.promo__discs {
  background-color: #dde9ea;
  padding: 27px 17px 24px 17px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
@media (min-width: 768px) {
  .promo__discs {
    padding: 30px 56px 25px 56px;
  }
}

@media (min-width: 768px) {
  .promo__content {
    display: grid;
    grid-template-columns: auto 1fr;
    gap: 32px;
  }
}

.promo__discs-photo-wrapper {
  margin-bottom: 16px;
}
.promo__discs-photo-wrapper img {
  display: block;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .promo__discs-photo-wrapper img {
    width: 229px;
    height: 231px;
  }
}

.promo__discs h2 {
  font-size: 20px;
  font-weight: 900;
  margin: 0 0 16px 0;
}
.promo__discs span {
  font-size: 13px;
  font-weight: 300;
}
@media (min-width: 768px) {
  .promo__discs {
    max-width: 405px;
  }
}

.promo__container {
  display: none;
}
@media (min-width: 768px) {
  .promo__container {
    display: block;
    overflow: hidden;
    position: relative;
  }
}

.promo__wrapper {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.promo__image-wrapper img {
  max-width: none;
  object-fit: cover;
}

.promo__swiper-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 19px;
  color: #828282;
  width: 50px;
  height: 47px;
  z-index: 1;
  padding: 10px;
  border: none;
  background-color: #e9e9e9;
  transition: all 0.3s ease;
  cursor: pointer;
}
.promo__swiper-button svg {
  transition: all 0.3s ease;
}
.promo__swiper-button:hover, .promo__swiper-button:focus, .promo__swiper-button:active {
  color: #ffffff;
  background-color: #ff812e;
}
.promo__swiper-button:hover svg, .promo__swiper-button:focus svg, .promo__swiper-button:active svg {
  color: #ffffff;
}
.promo__swiper-button:focus-visible {
  outline: none;
}
.promo__swiper-button:disabled {
  display: none;
}

.promo__swiper-button--prev {
  margin-right: 20px;
  left: 20px;
}
.promo__swiper-button--prev svg {
  transform: rotate(180deg);
}

.promo__swiper-button--next {
  right: 20px;
}

.promo__pagination-wrapper {
  position: absolute;
  bottom: 25px;
  right: 25px;
  z-index: 1;
}

.promo__pagination {
  display: flex;
}

.promo__bullet {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  min-width: 12px;
  min-height: 22px;
  padding: 5px 3px 3px 3px;
  text-align: center;
  font-size: 12px;
  font-weight: 900;
  line-height: 12px;
  color: #000000;
  background-color: rgba(255, 255, 255, 0);
  border-radius: 5px;
  cursor: pointer;
  user-select: none;
}

.promo__bullet:not(:last-child) {
  margin-right: 17px;
}

.promo__bullet--active {
  background-color: #ff812e;
}

.modal {
  max-width: 375px;
}

.modal__wrapper {
  display: flex;
  flex-direction: column;
  padding: 48px 46px;
}
.modal__wrapper h2 {
  font-size: 20px;
  font-weight: 900;
  margin: 0 0 32px 0;
}

.modal__fields-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 16px;
  align-self: stretch;
}
.modal__fields-wrapper input,
.modal__fields-wrapper textarea {
  border-radius: 12px;
  display: flex;
  padding: 14px;
  gap: 10px;
  align-self: stretch;
  border: none;
  background-color: #f5f5f5;
}
.modal__fields-wrapper input::placeholder,
.modal__fields-wrapper textarea::placeholder {
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}
.modal__fields-wrapper input {
  align-items: center;
}
.modal__fields-wrapper textarea {
  height: 167px;
  align-items: flex-start;
  flex-shrink: 0;
  resize: none;
}

.modal__fields-wrapper {
  margin-bottom: 32px;
}